/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react"
import PropTypes from "prop-types"
import { Helmet } from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"
import SiteImage from "../images/sunburst-logo.png"
import StoreImage from "../images/about.jpg"

function SeoBusiness({ description, lang, meta, title }) {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            keywords
            description
            url
            metaDescription
            author {
              name
              summary
            }
          }
        }
      }
    `
  )

  const metaDescription = description || site.siteMetadata.metaDescription
  const defaultTitle = site.siteMetadata?.title
  const keywords = site.siteMetadata?.keywords
  const logo = site.siteMetadata.url + SiteImage
  const storeImage = site.siteMetadata.url + StoreImage
  return (
  <Helmet
  htmlAttributes={{
    lang,
  }}
  title={title || defaultTitle}
  titleTemplate={title ? `${defaultTitle} | %s` : `${defaultTitle}`}
  meta={[
    {
      name: `description`,
      content: metaDescription,
    },
    {
      name: `keywords`,
      content: keywords
    },
    {
      property: `og:title`,
      content: title || defaultTitle,
    },
    {
      property: `og:description`,
      content: metaDescription,
    },
    {
      property: `og:type`,
      content: `website`,
    },
    {
      name: `twitter:card`,
      content: `summary`,
    },
    {
      name: `twitter:creator`,
      content: site.siteMetadata?.author.name || ``,
    },
    {
      name: `twitter:title`,
      content: title || defaultTitle,
    },
    {
      property: 'og:image',
      content: logo,
    },
    {
      name: 'twitter:image',
      content: logo,
    },
    {
      name: `twitter:description`,
      content: metaDescription,
    },
  ].concat(meta)}
>
  <script type="application/ld+json">{`
    "@context": "http://schema.org",
    "@type": "WebSite",
    "name": "${defaultTitle}",
    "url": "${site.siteMetadata.url}",
    "sameAs": [
      "https://www.yellowpages.com/etna-oh/mip/sunburst-pools-19572024",
      "https://www.facebook.com/Sunburst-Pools-166603570032111/"
    ]
  },
  {
    "@context": "https://schema.org",
    "@type": ["ProfessionalService", "Store"],
    "name": "${defaultTitle}",
    "legalName": "${defaultTitle}",
    "url": "${site.siteMetadata.url}",
    "logo": "${logo}",
    "image": "${storeImage}",
    "description": "Swimming pool contractor, wood pool builder, pool service company; Services include - build and service swimming pool, swimming pools, wood pools, above ground pools, in ground pools, pool, pool repair, pool service, pool parts, new pool, pool accessories, wood pool, on ground pool, pool deck, above ground pool, buy above ground pool online, pool for sale, swimming pool for sale, Pool Repair, Pool supplies, pool maintenance, pool sales",
    "founders": [
      {
        "@type": "Person",
        "name": "Joe Wagner"
      }
    ],
    "address": {
      "@type": "PostalAddress",
      "streetAddress": "12981 National Rd SW",
      "addressLocality": "Etna",
      "addressRegion": "OH",
      "postalCode": "43062",
      "addressCountry": "USA"
    },
    "geo": {
      "@type": "GeoCoordinates",
      "latitude": "39.956242",
      "longitude": "-82.729057"
    },
    "hasMap": "https://goo.gl/maps/dA4aH3JjTjdrXrAj6",
    "openingHours": "Mo 10:00-19:00 Tu 10:00-19:00 We 10:00-19:00 Th 10:00-19:00 Fr 10:00-19:00 Sa 10:00-19:00 Su 11:00-16:00",
    "telephone": "(740) 927-9686",
    "contactPoint": {
      "@type": "ContactPoint",
      "contactType": "customer support",
      "telephone": "+1-740-927-9686",
      "availableLanguage" : ["English"],
      "areaServed" : "US",
      "email": "info@sunburstpool.com"
    },
    "sameAs": [
      "https://www.yellowpages.com/etna-oh/mip/sunburst-pools-19572024",
      "https://www.facebook.com/Sunburst-Pools-166603570032111/"
    ]
  }
`}</script>
  </Helmet>
  )
}

SeoBusiness.defaultProps = {
  lang: `en`,
  meta: [],
  description: ``,
}

SeoBusiness.propTypes = {
  description: PropTypes.string,
  lang: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.object),
  title: PropTypes.string,
}

export default SeoBusiness
