import * as React from "react";
import { Link } from "gatsby";
import Layout from "../components/layout";
import SeoBusiness from "../components/seoBusiness";

const ContactUs = () => {
  const [name, setName] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [phone, setPhone] = React.useState("");
  const [message, setMessage] = React.useState("");
  const [nameError, setNameError] = React.useState(false);
  const [emailError, setEmailError] = React.useState(false);
  const [messageError, setMessageError] = React.useState(false);
  const [showModal, setShowModal] = React.useState(false);
  const [submitError, setSubmitError] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(false);
  const successMessage =
    "Your message was received. We will respond to you shortly.";
  const failureMessage = (
    <div>
      There was an error in the submission. Please reach us directly.{" "}
      <Link className="text-danger" to="/store">
        Store Hours
      </Link>
    </div>
  );
  const [submissionMessage, setSubmissionMessage] =
    React.useState(successMessage);

  const FORMSPREE_POST_URL = "https://formspree.io/f/mqkwoyrr";
  const emailRegex =
    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

  const showSuccessToast = () => {
    setIsLoading(false);
    setSubmissionMessage(successMessage);
    setSubmitError(false);
    setShowModal(true);
  };

  const showErrorToast = () => {
    setIsLoading(false);
    setSubmissionMessage(failureMessage);
    setSubmitError(true);
    setShowModal(true);
  };

  const isFormValid = () => {
    return !nameError && !emailError && !messageError;
  };

  const isFormEmpty = () => {
    return !(!!name && !!email && !!message);
  };

  const runFormValidation = () => {
    setNameError(name.trim() ? false : true);
    setEmailError(
      email.trim() && emailRegex.test(String(email.trim()).toLowerCase())
        ? false
        : true
    );
    setMessageError(message.trim() ? false : true);
  };

  const handleCloseModal = () => {
    if (!submitError) {
      resetForm();
    }
    setShowModal(false);
  };

  const resetForm = () => {
    setName("");
    setNameError(false);
    setEmail("");
    setEmailError(false);
    setPhone("");
    setMessage("");
    setMessageError(false);
    setSubmitError(false);
  };

  const submitForm = (event) => {
    setIsLoading(true);
    const formData = new FormData(event.target);
    formData.append("_subject", "Submission from website contact form!");
    const data = new URLSearchParams();
    for (const pair of formData) {
      data.append(pair[0], pair[1]);
    }
    fetch(FORMSPREE_POST_URL, {
      method: "POST",
      body: data,
      headers: {
        Accept: "application/json",
      },
    })
      .catch((e) => {
        showErrorToast();
      })
      .then((response) => {
        if (response.ok) {
          showSuccessToast();
        } else {
          showErrorToast();
        }
      });
  };

  const handleSubmitForm = (event) => {
    event.preventDefault();
    if (isFormEmpty()) {
      runFormValidation();
    } else if (isFormValid()) {
      submitForm(event);
    }
  };

  return (
    <Layout>
      <SeoBusiness title="Contact Us" />
      <section className="page-section cta">
        <div
          className="modal"
          tabIndex="-1"
          style={{ display: showModal ? "block" : "none" }}
          data-bs-backdrop="static"
          aria-labelledby="staticBackdropLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-dialog-centered modal-dialog-scrollable" >
            <div className="modal-content">
              <div
                class={
                  submitError
                    ? "modal-header text-white bg-danger"
                    : "modal-header text-white bg-success"
                }
              >
                <h5 class="modal-title" id="staticBackdropLabel">
                  {submitError ? "Message failed" : "Message sent"}
                </h5>
                <button
                  type="button"
                  class="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                  onClick={() => setShowModal(false)}
                ></button>
              </div>
              <div className="modal-body">
                <p>{"Hello " + name + ","}</p>
                <p>{submissionMessage}</p>
                <p>Thank you</p>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  class="btn btn-primary btn-lg"
                  data-bs-dismiss="modal"
                  onClick={handleCloseModal}
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="col-xl-9 mx-auto">
              <div className="cta-inner bg-faded rounded">
                <h2 className="section-heading mb-5 text-center">
                  <span className="section-heading-upper">Send us a note</span>
                  <span className="section-heading-lower">Contact Us</span>
                </h2>
                <form
                  id="contact-form"
                  noValidate
                  onSubmit={handleSubmitForm}
                  onChange={runFormValidation}
                >
                  <div className="mb-3 form-group">
                    <label htmlFor="name" className="form-label">
                      Name *
                    </label>
                    <input
                      id="name"
                      name="name"
                      type="text"
                      onChange={(event) => setName(event.target.value)}
                      value={name}
                      className={
                        "form-control" + (nameError ? " is-invalid" : "")
                      }
                      placeholder="Your name"
                    />
                    <div className="invalid-feedback">A name is required</div>
                  </div>
                  <div className="mb-3">
                    <label htmlFor="email" className="form-label">
                      Email *
                    </label>
                    <input
                      type="email"
                      id="email"
                      name="_replyTo"
                      value={email}
                      onChange={(event) => setEmail(event.target.value)}
                      placeholder="Your email"
                      className={
                        "form-control" + (emailError ? " is-invalid" : "")
                      }
                    />
                    <div className="invalid-feedback">
                      A valid email is required
                    </div>
                  </div>
                  <div className="mb-3">
                    <label htmlFor="email" className="form-label">
                      Phone
                    </label>
                    <input
                      type="phone"
                      id="phone"
                      name="telephone"
                      value={phone}
                      placeholder="Your phone #"
                      className="form-control"
                      onChange={(event) => setPhone(event.target.value)}
                    />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="message" className="form-label">
                      Message *
                    </label>
                    <textarea
                      rows="4"
                      id="message"
                      name="message"
                      value={message}
                      placeholder="Your message to us"
                      className={
                        "form-control" + (messageError ? " is-invalid" : "")
                      }
                      onChange={(event) => setMessage(event.target.value)}
                    />
                    <div className="invalid-feedback">
                      A message is required
                    </div>
                  </div>
                  <div className="intro-button mx-auto d-flex justify-content-end">
                  
                    <button
                      type="submit"
                      // style={{ fontSize: "1em" }}
                      className="btn btn-lg btn-primary mb-3 d-flex align-items-center"
                    >
                      { isLoading ? 
                      <div class="spinner-border" style={{color: "#f6e1c5"}} role="status">
                        <span class="visually-hidden">Loading...</span>
                      </div>
                      :
                      <>
                      <span className="me-1">Send</span>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="16"
                        height="16"
                        fill="currentColor"
                        className="bi bi-envelope-fill"
                        viewBox="0 0 16 16"
                      >
                        <path d="M.05 3.555A2 2 0 0 1 2 2h12a2 2 0 0 1 1.95 1.555L8 8.414.05 3.555zM0 4.697v7.104l5.803-3.558L0 4.697zM6.761 8.83l-6.57 4.027A2 2 0 0 0 2 14h12a2 2 0 0 0 1.808-1.144l-6.57-4.027L8 9.586l-1.239-.757zm3.436-.586L16 11.801V4.697l-5.803 3.546z" />
                      </svg>
                      </>
                    }
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default ContactUs;
